<template>
<div class="usercenter-container">
  <van-nav-bar
    title="个人中心"
  />
  <!-- <div class="mask" v-if="!isLogin" @click="toLogin"></div> -->
  <div class="header">
    <div class="userinfo">
      <div class="avatar">
      </div>
      <div class="right">
        <div v-if="!isLogin" @click="goto('pointsRecord')">点击登录</div>
        <!-- <open-data v-if="isLogin" class="name" type="userNickName"></open-data> -->
        <div v-if="isLogin" class="name">{{user.realName}}</div>
        <div v-if="isLogin" class="level">级别：{{user.category===1?'代理':'经销商'}}</div>
      </div>
    </div>
    <div class="point">
      <van-image class="point-icon" :src="img0"></van-image>
      <text>积分：{{user.mallPoints||0}}</text>
    </div>
  </div>
  <div class="btn-box">
    <div class="active-btn" data-scanType="active" bindtap="scan">
      <van-icon name="scan" size="24px" color="fe5002" />
      <text>{{user.category === 1 ? '扫码获取积分':'扫码激活'}}</text>
    </div>
    <div class="divide-line"></div>
    <div class="check-btn" data-scanType="check" bindtap="scan">
      <van-icon name="scan" size="24px" color="#00b034" />
      <text>防伪查询</text>
    </div>
  </div>
  <div class="d-line"></div>
  <div class="cell-box">
    <van-cell is-link to="exchangeRecord">
      <template #title>
        <div class="van-cell-text">
          <van-image class="icon" :src="imgurl.zero"/>
          <text>兑换记录</text>
        </div>
      </template>
    </van-cell>
    <van-cell is-link to="pointsRecord">
      <template #title>
        <div class="van-cell-text" >
          <van-image class="icon" :src="imgurl.one"/>
          <text>积分记录</text>
        </div>
