      <van-tabbar-item icon="user-o" replace to="/mine">个人中心</van-tabbar-item>
    </van-tabbar>
</div>
</template>

<script>
import img0 from '../assets/images/point.png'
import img1 from '../assets/images/exchange-record.png'
import img2 from '../assets/images/point-record.png'
import img3 from '../assets/images/order.png'
import img4 from '../assets/images/location_2.png'
import {
  QureyCode, ActiveCode
} from '@/api'
import router from '../router'
import {reactive,toRefs} from 'vue'
export default {
  name: 'mine',
  setup(props) {
    //参数
    const code = 0
    let mine = reactive({
      isLogin: false,
      userType: 2,
      user: { category: 1 },
      active:1,
      img0:img0,
      imgurl:{
        zero:img1,
        one:img2,
        two:img3,
        three:img4,
      }
    })
    const user = localStorage.getItem('userinfo')
    const token = localStorage.getItem('token')
    if (user) {
     mine.user = JSON.parse(user)
    }
    mine.isLogin = (token !== '')
    console.log('a');
    function toLogin(a){
      Dialog.confirm({
        title: '登录提示',
        message: '您还没有登录，点击确定去登录',
      })
        .then(() => {
          router.push('login')
          // on confirm
        })
        .catch(() => {
          // on cancel
        });
      console.log('get category==>', a)
    }
    //挂载
    return{ toLogin,...toRefs(mine)}
  }
}
</script>

